.work-detail__input {
    width: 1226px;
    height: 89px;
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    background: #F8F7FA;
    border: 0;
    outline: none;
    padding-left: 12px;
    border-bottom-right-radius: 2px;
    border-bottom-left-radius: 2px;
    resize: none;
    &:focus {
        background-color: #c7c7c7;
    }
}